import React from 'react'

export default class Address extends React.Component {

  constructor(props){
    super(props);
    this.state = {zipcode:'', route: '', address: ''}
  }

  render(){
    return (
      <div>
        <input type="text" placeholder='zipcode' value={this.state.zipcode}
          onChange={e => {this.setState({zipcode: e.target.value})}}
        />
        <input type="text" placeholder='route' value={this.state.route}
          onChange={e => {this.setState({route: e.target.value})}}
        />
        <input type="text" placeholder='address' value={this.state.address}
          onChange={e => {this.setState({address: e.target.value})}}
        />
        <button onClick={() => {
          this.props.saveRecord(this.state);
        }}>Save</button>
      </div>
    )
  }

}


