import React, {useEffect} from 'react'
import { GoogleMap, useJsApiLoader, Marker } from '@react-google-maps/api';
import CryptoJS from 'crypto-js';
import Address from './components/Address';


const containerStyle = {
  width: '400px',
  height: '400px'
};


const code =prompt("enter secret code");
var decrypted = CryptoJS.AES.decrypt("U2FsdGVkX19lAWpb/EuEWDRvdoqTQDir8Ash90/EVTL9Ynn3M2TTdzFrxgGb9XRr9T0S0ABxY9uUgEwQlf/JmA==", code).toString(CryptoJS.enc.Utf8);


function MyComponent() {
  const { isLoaded } = useJsApiLoader({
    id: 'google-map-script',
    googleMapsApiKey: decrypted
  });


  const [map, setMap] = React.useState(null)
  const [lat, setLat] = React.useState(0);
  const [lng, setLng] = React.useState(0);
  const [status, setStatus] = React.useState(null);
  const [markers, setMarkers]= React.useState([]);

  useEffect(() => {
    let data = localStorage.getItem("markers");
    if(data){
      setMarkers(JSON.parse(data));
      getLocation();
    }

  }, [])
   
  const getLocation = () => {
    if (!navigator.geolocation) {
      setStatus('Geolocation is not supported by your browser');
    } else {
      setStatus('Locating...');
      navigator.geolocation.getCurrentPosition((position) => {
        setStatus(null);
        setLat(position.coords.latitude);
        setLng(position.coords.longitude);
      }, () => {
        setStatus('Unable to retrieve your location');
      });
    }
  }

  const onLoad = React.useCallback(function callback(map) {
    // This is just an example of getting and using the map instance!!! don't just blindly copy!
    const bounds = new window.google.maps.LatLngBounds({lat,lng});
    map.fitBounds(bounds);

    setMap(map)
  }, [])

  const onUnmount = React.useCallback(function callback(map) {
    setMap(null)
  }, [])

  const saveRecord= (record) => {
    const {route, zipcode, address} = record;
    console.log(record, lat, lng);
    console.log('***', markers);
    setMarkers(markers.concat([
      {
        address: record.address, zipcode: record.zipcode, route: record.route,
        coordinate: { lat: lat, lng: lng}
      }
    ]));
    
  
    
      
  }

  return isLoaded ? (
    <>
      <GoogleMap
        mapContainerStyle={containerStyle}
        center={{lat, lng}}
        zoom={10}
        onLoad={onLoad}
        onUnmount={onUnmount}
      >
        { /* Child components, such as markers, info windows, etc. */ }
        {markers.length >0 && markers.map((marker, index) => (
                        <Marker
                        key ={index}
                        position={marker.coordinate}
                       
                        title={marker.address}
                        />
                    ))}
        <></>
        
      </GoogleMap>
      <div className="App">
        <button onClick={getLocation}>Get Location</button>
        <h1>Coordinates</h1>
        <p>{status}</p>
        {lat && <p>Latitude: {lat}</p>}
        {lng && <p>Longitude: {lng}</p>}
      </div>
      <Address saveRecord={saveRecord}/>
      <div>
        <button onClick={() => {
           const dataJSON = localStorage.getItem("markers");
           const data = JSON.parse(dataJSON)
           if(data){
               setMarkers(data);
           }
           alert(data);
        }}>Get from storage</button>
        <button onClick={(record) => {
          localStorage.setItem("markers", JSON.stringify(markers));
        }}>store data</button>
      </div>
      </>
  ) : <>
  
  
  </>
}

export default React.memo(MyComponent)